.main{
    min-height: 100vh;
    min-width: 100%;
    background-image: url(./images/background1.jpg);
    background-size: cover;
    background-attachment: fixed;
    color:white;
}
.wrapper {
    width: 50%;
    margin: 0 auto;
    padding-top: 3rem;
}
.head-title{
    text-align: center;
}
.head-title h1{
    top: 0;
    margin: 0;
    padding: 0;
}
.info-button{
    color:white;
    background:none;
    border: 2px solid #ffb200;
    border-radius:20px;
}
.info-button:active, .info-button:focus{
    outline: none;
}
.info-button:hover{
    color: white;
    background-color:#ffb200;
}
.infb2-p{
    margin-left: 20px;
}
.infb2-s{
    min-width: 100px;
    height: 30px;
}
.infb1-s{
    min-width: 140px;
    height: 40px;
}
.input-add{
    color:white;
    padding: 7px;
    background:none;
    border:2px solid white;
}
.input-add:focus{
    outline: none;
    padding:7px;
    border:2px solid #ffb200;
}
.li-items{
    width: 100%;
}
.li-items span{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.li-items:hover{
    background-color:rgba(255, 255, 255, .1);
    box-shadow: 0 0 5px #4d3703a9,
                0 0 15px #4d3703a9,
                0 0 50px #4d3703a9,
                0 0 200px #4d3703a9
}
.text-todo{
    padding: 0 0 0 15px;
}
.text-todo p{
    overflow-wrap: break-word;
}
.rm {
    width: 50px;
    height: 40px;
    font-size: 30px;
    color: #ffb200;
    border: none;
    border-radius: 10%;
    background: none;
    cursor: pointer;
}

.done {
    text-decoration: line-through;
}
.chkbox{
    font-size: 18px;
}
.chkbox input {
    display: none;
  }
.chkbox span {
    min-height: 13px;
    min-width: 13px;
    border: 1px solid gray;
    display: inline-block;
    position: relative;
    background-color:none;
    border-radius:2px;
    padding:3px;
    margin-left:15px;
    margin-right: 15px;
}
.chkbox [type=checkbox]:checked + span:before {
    content: '\2714';
    position: absolute;
    top: -5px;
    left: 0;
    font-size:23px;
    color:#ffb200;
  }

.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
}

.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #fffa;
    border-color: #aaa transparent #aaa transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}