.modal {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}

.modal-body {
  color: black;
  padding: 2rem;
  width: 400px;
  border-radius: 5px;
  background: #fff;
  height: 220px;
}
.anim-button {
    width:113px;
    height: 60px;
    margin-top: 30px;
    margin-left: 37%;
  text-align: center;
}
.anim-button a {
  text-align: center;
  position: relative;
  display: inline-block;
  padding: 20px 25px;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 3px;
  cursor: pointer;
  overflow: hidden;
}
.anim-button a span {
  position: absolute;
  display: block;
}
.anim-button a span:nth-child(1) {
  top: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, #110c01, transparent);
  animation: animate1 1s linear infinite;
}
@keyframes animate1 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}
.anim-button a span:nth-child(4) {
  left: 0;
  top: -100%;
  height: 100%;
  width: 2px;
  background: linear-gradient(180deg, transparent, #110c01);
  animation: animate4 1s linear infinite;
  /* animation-delay: .25s; */
}
@keyframes animate4 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}
.anim-button a span:nth-child(3) {
  bottom: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #110c01);
  animation: animate3 1s linear infinite;
  /* animation-delay: .5s; */
}
@keyframes animate3 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}
.anim-button a span:nth-child(2) {
  bottom: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, #110c01, transparent);
  animation: animate2 1s linear infinite;
  /* animation-delay: .75s; */
}
@keyframes animate2 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
